import { useCallback } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import LmsLeaveRequest from "./LmsLeaveRequest";
import LmsLeaveIssued from "../LmsLeaveIssued/LmsLeaveIssued";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";

const LmsLeaveRequestConfig = ({ lmid, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();

  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/lms`
        : `/q/${params.username}/member/staff/lms`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div
            className={style.fm_header_start_container}
            style={{
              gap: "0.2rem",
            }}
          >
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{t("leave_request")}</h6>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/lms?type=request${tab.urlKey}`
                : `/q/${params.username}/member/staff/lms?type=request${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {searchParams.get("n") === "issued" ? (
          <LmsLeaveIssued lmid={lmid} instituteId={instituteId} />
        ) : (
          <LmsLeaveRequest lmid={lmid} instituteId={instituteId} />
        )}
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper>
  );
};

export default LmsLeaveRequestConfig;

const tabList = [
  {
    label: "request",
    id: "request",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "issued",
    id: "issued",
    urlKey: "&n=issued",
    groupIs: ["issued"],
  },
];
