import { useCallback, useEffect, useState } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useLmsLeaveRequestList } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import LmsLeaveRequestCard from "./LmsLeaveRequestCard";

const LmsLeaveRequest = ({ instituteId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    lmsLeaveRequestList,
    lmsLeaveRequestListLoading,
    lmsLeaveRequestListRefetch,
  } = useLmsLeaveRequestList({
    data: {
      id: instituteId,
      page: page,
      limit: 10,
      search: search,
      flow: "Request",
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (lmsLeaveRequestList?.list?.length) {
      let obj = nextPage(lmsLeaveRequestList?.list?.length);
      setState(() => obj);
    }
  }, [lmsLeaveRequestList?.list]);

  const onSearchEvent = (e) => {
    if (!lmsLeaveRequestListLoading && lmsLeaveRequestList?.list?.length >= 0) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    lmsLeaveRequestListRefetch();
  }, [lmsLeaveRequestListRefetch]);
  return (
    <>
      <div className={style.fm_search_full}>
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>
        </section>
      </div>
      {lmsLeaveRequestList?.list?.map((leave, index) =>
        lmsLeaveRequestList?.list?.length === index + 1 ? (
          <div key={leave?._id} ref={ref}>
            <LmsLeaveRequestCard leave={leave} onRefetch={onRefetch} />
          </div>
        ) : (
          <LmsLeaveRequestCard
            leave={leave}
            key={leave?._id}
            onRefetch={onRefetch}
          />
        )
      )}
      {lmsLeaveRequestListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        lmsLeaveRequestList?.list?.length === 0 && (
          <EmptyMessage
            title="No staff leave request found related this search."
            customStyleContainer={{
              marginTop: "2rem",
            }}
          />
        )
      ) : (
        lmsLeaveRequestList?.list?.length === 0 && (
          <EmptyMessage
            title="No staff leave request."
            customStyleContainer={{
              marginTop: "2rem",
            }}
          />
        )
      )}
    </>
  );
};

export default LmsLeaveRequest;
